.formula {
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
  background-image: linear-gradient(to bottom, #f3f3f8, $white);
}

.formula__title {
  margin: 0 auto 30px;
  max-width: 620px;
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
}

.formula__desc {
  margin: 0 auto 30px;
  max-width: 620px;
  font-size: 18px;
  line-height: 25px;
  color: $dark-gray;
}

.formula__note {
  margin: 0 auto 60px;
  max-width: 620px;
  color: $dark-gray;
}

.formula__icon {
  margin-bottom: 12px;
}

.formula__price-value {
  margin: 0;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 70px;
  line-height: 67px;
  color: $red;
}

.formula__price-label {
  margin: 0;
  margin-bottom: 40px;
  font-weight: bold;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: $gray;
}

.formula__list {
  @include list-reset;

  @include desktop {
    max-width: 1016px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.formula__item {
  padding: 10px 0;

  @include desktop {
    padding: 10px;
    width: 50%;
  }
}

.formula__box {
  height: 100%;
  padding: 40px 18px 24px;
  background: $white;
  border: 1px solid $light-gray;
  border-radius: 20px;

  @include desktop {
    border-radius: 50px;
  }
}

.formula__box-title {
  margin: 0;
  margin-bottom: 24px;
  padding: 0 20px;
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
}

.formula__box-calc {
  margin: 0;
  padding: 6px 0;
  position: relative;
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  color: $red;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 88px;
    height: 2px;
    background-color: $light-gray;
  }

  &--text {
    @include desktop {
      padding: 28px 0;
    }
  }

  &--text::before {
    display: none;
  }

  .gray {
    margin: 0 6px;
  }
}

.formula__box-numer,
.formula__box-demon {
  display: block;
  padding: 8px;

  span {
    color: $gray;
  }
}

.formula__box-calc-desc {
  margin: 16px auto;
  max-width: 326px;
  color: $dark-gray;

  @include desktop {
    text-align: left;
  }

  strong {
    color: $dark;
  }
}

.formula__box-subtitle {
  margin: 24px auto 17px;
  max-width: 326px;
  font-weight: 700;
  color: $main;

  @include desktop {
    text-align-last: left;
  }
}
